import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
const CreateResponse = import("../views/CreateResponse.vue");
const ViewResponse = import("../views/ViewResponse.vue");
const EditResponse = import("../views/EditResponse.vue");
const ViewAllResponses = import("../views/ViewAllResponses.vue");
const ViewForms = import("../views/ViewForms.vue");
const CreateForm = import("../views/CreateForm.vue");
const CreateEvent = import("../views/CreateEvent.vue");
const EventDetails = import("../views/EventDetails.vue");
const ChooseForm = import("../views/ChooseForm.vue");
const MyInscriptions = import("../views/MyInscriptions.vue");
const ViewEvents = import("../views/ViewEvents.vue");
const InicialPage = import("../views/mobile/InicialPage.vue");
const EventDetailsParticipant = import("../views/mobile/EventDetailsParticipant.vue");
const EventDetailsAdmin = import("../views/mobile/EventDetailsAdmin.vue");
const ConfirmParticipants = import("../views/mobile/ConfirmParticipants.vue");
const Checkin = import("../views/CheckIn.vue");
const ValidateSignup = import("../views/ValidateSignup.vue");

const routes: Array<RouteRecordRaw> = [
  {
    path: "",
    name: "ViewForms",
    component: ViewForms,
  },
  {
    path: "/respond/:id",
    name: "CreateResponse",
    component: CreateResponse,
  },
  {
    path: "/view_response/:id",
    name: "View Response",
    component: ViewResponse,
  },
  {
    path: "/edit_response/:id",
    name: "Edit Response",
    component: EditResponse,
  },
  {
    path: "/responses/:id",
    name: "View All Responses",
    component: ViewAllResponses,
  },
  {
    path: "/createForm/:id?",
    name: "Create Form",
    component: CreateForm,
  },
  {
    path: "/createEvent",
    name: "Create Event",
    component: CreateEvent,
  },
  {
    path: "/eventDetails/:id",
    name: "Event Details",
    component: EventDetails,
  },
  {
    path: "/chooseForm/:id",
    name: "Choose Form",
    component: ChooseForm,
  },
  {
    path: "/myInscriptions/:id",
    name: "My Inscriptions",
    component: MyInscriptions,
  },
  {
    path: "/viewEvents/:id",
    name: "View Events",
    component: ViewEvents,
  },
  {
    path: "/inicialPage",
    name: "Inicial Page",
    component: InicialPage,
  },
  {
    path: "/eventDetailsParticipant/:id",
    name: "Event Details Participant",
    component: EventDetailsParticipant,
  },
  {
    path: "/eventDetailsAdmin/:id",
    name: "Event Details Admin",
    component: EventDetailsAdmin,
  },
  {
    path: "/confirmParticipants/:id",
    name: "Confirm Participants",
    component: ConfirmParticipants,
  },
  {
    path: "/checkin/:id",
    name: "Checkin",
    component: Checkin,
  },
  {
    path: "/validatesignup/:id",
    name: "ValidateSignup",
    component: ValidateSignup,
  },
];

const router = createRouter({
  history: createWebHistory("/forms/"),
  routes,
});

export default router;
